<!--在线测评 -->
<template>
  <div class="testpaper" style="margin:0 100px;">
        <div class="testpapername">初三物理测试</div>
        <div class="testpapertips">本套试卷共10个题目，每次考试随机抽取5个题目进行考试，每题20分，共100分。考试时间5分钟。</div>
        <div class="testpaperdashed" ></div>
        <div class="testpapercontent">
          <div class="testpaperqusetion"><span style="vertical-align: middle;color:red;">*</span>1.    沙子从h=0.8m高处落到以3m/s速度水平向右运动的传送带上。取g=10m<sup>2</sup>,则传送带给予沙子的作用力的方向</div>
          <div class="testpaperanswer">
            <el-radio-group v-model="answers[0]" style="width:100%;">
              <el-radio label="A">A、与水平夹角53&deg;向下</el-radio>
              <el-radio label="B">B、与水平夹角53&deg;向上</el-radio>
              <el-radio label="C">C、与水平夹角37&deg;向下</el-radio>
            </el-radio-group>
          </div>
        </div>
        <div class="testpapercontent">
          <div class="testpaperqusetion"><span style="vertical-align: middle;color:red;">*</span>2.    下半旗是把旗子下降到</div>
          <div class="testpaperanswer">
            <el-radio-group v-model="answers[1]" style="width:100%;">
              <el-radio label="A">A、1/3处</el-radio>
              <el-radio label="B">B、1米</el-radio>
              <el-radio label="C">C、1/2处</el-radio>
            </el-radio-group>
          </div>
        </div>
        <div class="testpapercontent">
          <div class="testpaperqusetion"><span style="vertical-align: middle;color:red;">*</span>3.    下列著名宫殿哪个位于英国</div>
          <div class="testpaperanswer">
            <el-radio-group v-model="answers[2]" style="width:100%;">
              <el-radio label="A">A、凡尔赛宫</el-radio>
              <el-radio label="B">B、克里姆林宫</el-radio>
              <el-radio label="C">C、白金汉宫</el-radio>
            </el-radio-group>
          </div>
        </div>
        <div class="testpapercontent">
          <div class="testpaperqusetion"><span style="vertical-align: middle;color:red;">*</span>4.    路透社是哪个国家的通讯社？</div>
          <div class="testpaperanswer">
            <el-radio-group v-model="answers[3]" style="width:100%;">
              <el-radio label="A">A、英国</el-radio>
              <el-radio label="B">B、法国</el-radio>
              <el-radio label="C">C、德国</el-radio>
            </el-radio-group>
          </div>
        </div>
        <div class="testpapercontent">
          <div class="testpaperqusetion"><span style="vertical-align: middle;color:red;">*</span>5.    某人以4km/h的速率向东前进时,感觉风从正北吹来,如将速率增加一倍,则感觉风从东北吹来。实际风速与风向为</div>
          <div class="testpaperanswer">
            <el-radio-group v-model="answers[4]" style="width:100%;">
              <el-radio label="A">A、4km/h,从西北方吹来</el-radio>
              <el-radio label="B">B、4√<span style="text-decoration:overline;">2</span>km/h,从北方吹来</el-radio>
              <el-radio label="C">C、4√<span style="text-decoration:overline;">2</span>km/h,从西北方吹来</el-radio>
            </el-radio-group>
          </div>
        </div>
        <button class="testpapersubmit" @click="testpapersubmit">交卷</button>
  </div>
</template>
<script>
export default {
  name: 'asses-view',
  data() {
    return {
      subjectID: '',
      answers: ['', '', '', '', ''], // 回答的答案
    }
  },
  created() {
    this.$emit('header', false);
    this.$emit('footer', false);
    if (this.$route.query.subjectID) {
      this.subjectID = this.$route.query.subjectID;
    }
  },
  methods: {
    testpapersubmit() {
      const hasnull = this.answers.indexOf('');
      if (hasnull !== -1) {
        this.$alert(`第${hasnull + 1}题尚未完成，请完成所有题目再进行交卷！`, '提示', {
          confirmButtonText: '确定',
        })
      } else {
        this.$confirm('确认交卷吗?', '提示', {
          confirmButtonText: '交卷',
          cancelButtonText: '再想想',
          type: 'info',
        }).then(() => {
          this.$message({
            type: 'success',
            message: '交卷成功!',
          });
          let score = 100; // 分数
          // 正确的答案
          const rightanswers = ['B', 'A', 'C', 'A', 'C']
          const erroranswes = [];// 回答错误的答案
          for (let i = 0; i < rightanswers.length; i += 1) {
            if (rightanswers[i] !== this.answers[i]) {
              score -= 20;
              erroranswes.push({ index: i, value: this.answers[i] });
            }
          }
          this.$router.push({ path: '/assessresult', query: { subjectID: '3-3-1', totalscore: score } })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '取消交卷',
          });
        });
      }
    },
  },
}
</script>
<style scoped>
  .testpapername{
    margin-top: 45px;
    line-height: 52px;
    color: rgba(8, 150, 255, 1);
    font-size: 36px;
    text-align: center;
    font-family: Microsoft Yahei;
  }
  .testpapertips{
    margin-top: 45px;
    line-height: 52px;
    font-size: 24px;
    font-family: Microsoft Yahei;
  }
  .testpaperdashed{
    width: 100%;
    height: 1px;
    background-image: linear-gradient(to right, #ccc 0%, #ccc 50%, transparent 50%);
    background-size: 8px 1px;
    background-repeat: repeat-x;
    margin-top: 15px;
  }
  .testpapercontent{
    padding: 70px 0px 34px 0px;
    border-bottom: solid 1px #EFEFEF;
  }
  .testpaperqusetion{
    font-size: 24px;
    font-weight: bold;
    font-family: Microsoft Yahei;
    white-space: pre;
  }
  .testpaperanswer{
    margin-top: 15px;
    font-size:24px;
  }
  .testpaperanswer>>>.el-radio {
    color: #000000;
    font-weight: 500;
    line-height: 1;
    cursor: pointer;
    white-space: nowrap;
    outline: 0;
    width: 30%;
}
  .testpaperanswer>>>.el-radio__input {
    white-space: nowrap;
    cursor: pointer;
    outline: 0;
    line-height: 1;
    vertical-align: middle;
    height: 24px;
}
.testpaperanswer>>>.el-radio__label {
    font-size: 14px;
    font-size: 24px;
    padding-left: 10px;
}
.testpapersubmit{
  display: block;
  margin: 50px auto 61px auto;
  width: 180px;
  height: 80px;
  line-height: 44px;
  border-radius: 18px;
  background-color: rgba(8, 150, 255, 1);
  color: rgba(255, 255, 255, 1);
  font-size: 30px;
}
</style>



